.contact {
    /* background: linear-gradient(90deg in oklab, rgb(23, 23, 65), rgb(149, 34, 34)); */


    margin-top: 20px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    width: 400px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.contact h1 {
    margin-top: 20px;
    text-align: center;
}

.form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
}

.form input {
    height: 50px;
    width: 300px;
    margin-top: 20px;
    padding-left: 15px;
    outline: none;



}

.button {
    background-color: #405cf5;
    box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .1) 0 2px 5px 0, rgba(0, 0, 0, .07) 0 1px 1px 0;
    outline: none;
    border: 0;
    width: 300px;
    font-weight: 600;
    border-radius: 30px;
    color: #fff;
}

.btn {
    appearance: button;
    backface-visibility: hidden;
    background-color: #405cf5;

    border-width: 0;

    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-family: -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", Ubuntu, sans-serif;
    font-size: 100%;
    height: 44px;
    line-height: 1.15;
    margin: 12px 0 0;
    outline: none;
    overflow: hidden;
    padding: 0 25px;
    position: relative;
    text-align: center;
    text-transform: none;
    transform: translateZ(0);
    transition: all .2s, box-shadow .08s ease-in;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;

}

.btn:disabled {
    cursor: default;
}

.btn:focus {
    box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}